@import "./mixins.scss";

.errorPanelContainer {
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 5%;
  width: 90%;
  height: 95%;
  background-color: rgba(255, 255, 255, 0.6);
  color: black;
  box-shadow: 0 0 5px 2px black;
  overflow: hidden;
  @media (orientation: landscape) {
    height: 85%;
    flex-direction: row;
  }

  &--night {
    box-shadow: 0 0 5px 2px white;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
  }
  img {
    padding: 10% 0 0;
    width: 70%;
    @media (orientation: landscape) {
      width: 40%;
      padding: 0 0 0 5%;
    }
  }
}
