edia (min-width: 1200px){.formContainer__form__button{padding:3% 6%}.formContainer__form__button:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}}.formContainer__form__button--night{background-color:rgba(255,255,255,0.8);border:1px solid white;box-shadow:0 0 5px 2px white;color:black}

