}.weatherPanelContainer__panel__timeAndDescription:last-of-type{padding-bottom:3%}.weatherPanelContainer__panel__strong{border-top:1px solid black;font-size:70px;width:90%;text-align:center}@media (orientation: landscape){.weatherPanelContainer__panel__strong{width:45%}}@media (min-width: 640px){.weatherPanelContainer__panel__strong{font-size:90px}}@media (min-width: 800px){.weatherPanelContainer__panel__strong{font-size:110px}}@media (min-width: 1024px){.weatherPanelContainer__panel__strong{font-size:140px}}@media (min-width: 1200px){.weatherPanelContainer__panel__strong{font-size:170px}}@media (min-width: 1440px){.weatherPanelContainer__panel__strong{font-size:200px}}.weatherPanelContainer__panel__strong--night{border-top:1px solid white}.weatherPanelContainer__panel .otherParametersContainer{display:flex;flex-grow:1;font-size:14px;flex-direction:column;width:90%}@media (orientation: landscape){.weatherPanelContainer__panel .otherParametersContainer{width:50%;align-self:flex-start;position:absolute;bottom:0;left:0;padding:0 0 0.5% 2%;font-size:10px}}@media (min-width: 640px){.weatherPanelContainer__panel .otherParametersContainer{font-size:12px}}@media (min-width: 800px){.weatherPanelContainer__panel .otherParametersContainer{font-size:14px}}@media (min-width: 1024px){.weatherPanelContainer__panel .otherParametersContainer{font-size:16px}}@media (min-width: 1200px){.weatherPanelContainer__panel .otherParametersContainer{font-size:18px}}.weatherPanelContainer__panel .otherParametersContainer__element{display:flex;justify-content:space-evenly;padding:2% 0;border-bottom:1px solid black}.weatherPanelContainer__panel .otherParametersContainer__element--night{border-bottom:1px solid white}.weatherPanelContainer__panel .otherParametersContainer__element__description{display:flex;flex-direction:column;text-align:center}.weatherPanelContainer__panel .otherParametersContainer__element__description__value{padding-top:2%;font-weight:bold}.weatherPanelContainer__panel .otherPa